.container {
  position: relative;
  display: flex;
  flex-direction: column;
  border: 1px solid black;
  max-width: 30rem;
  min-height: fit-content;
}
.head {
  font-size: 1.7rem;
}
.tag {
  font-size: 1.3rem;
  color: gray;
}
.desc {
  font-size: 1.5rem;
}
.price {
  font-size: 1.5rem;
}
.c-price {
  line-height: 1.5;
}

.off {
  color: red;
  line-height: 1.5;
}
.f-box {
  display: flex;
}
.ht {
  flex-direction: column;
}
.ff {
  justify-content: space-between;
}
.hof {
  position: absolute;
  bottom: 9rem;
}
.tof {
  position: absolute;
  bottom: 7rem;
}
.OB {
  position: absolute;
  top: 2.5rem;
  left: 0;
  background-color: black;
  color: cornsilk;
}
